import React from 'react'
import { withStyles, Typography } from 'material-ui'
import Separator from '../../components/Separator'
import Bullet from '../../components/Bullet'
import Content from '../../components/Content'

const styles = theme => ({
  container: {
    marginTop: 30,
  },
  header: {
    color: theme.palette.primary.main,
    fontWeight: 700,
    fontSize: 24,
  },
  primary: {
    fontWeight: 700,
    color: theme.palette.primary.main,
  },
  separator: {
    width: '60%',
    margin: '15px auto',
  },
})

const Intervenir = ({ classes }) => (
  <Content id="intervenir" className={classes.container}>
    <Typography className={classes.header} align="center" gutterBottom>
      Comment nous pouvons intervenir ?
    </Typography>
    <Typography align="center" gutterBottom>
      Un projet dinnovation collaboratif en trois tapes pour une dmarche
      pragmatique QuickWin :
    </Typography>
    <Separator className={classes.separator} />
    <Typography variant="body2" gutterBottom>
      <Bullet>1</Bullet> Atelier / Diagnostic pour dfinir le primtre,
      identifier puis rduire le diffrentiel entre ltat de lart et votre
      systme de production actuel.
    </Typography>
    <Typography variant="body2" gutterBottom>
      <Bullet>2</Bullet> Mise en uvre rapide dun POC (moins de 3 mois) sur vos
      donnes pour valuer les solutions IT envisages et les outils danalyse
      retenus.
    </Typography>
    <Typography variant="body2" gutterBottom>
      <Bullet>3</Bullet> Dploiement pour industrialiser et tendre la dmarche.
      Accompagnement  la conduite du changement.
    </Typography>
  </Content>
)

export default withStyles(styles)(Intervenir)



